.logo {
  padding: 0px 15px 0;
  margin: 0;
  z-index: 2;

  &.bg-dark {
    background-color: theme(color, dark);
  }

  a {
    display: inline-block;

    img {
      width: 100px;
    }
  }

  /***
  *** tablet portrait ***
  ***/
  @media #{theme(media-queries, tablet-portrait)} {
    position: absolute;

    a img {
      width: 125px;
    }
  }

  /***
  *** desktop ***
  ***/
  @media #{theme(media-queries, desktop)} {
    a img {
      width: 150px;
    }
  }

  /***
  *** desktop xl***
  ***/
  @media #{theme(media-queries, desktop-xl)} {
    a img {
      width: 180px;
    }
  }

  /***
  *** desktop xxl***
  ***/
  @media #{theme(media-queries, desktop-xxl)} {
    a img {
      width: 200px;
    }
  }
}
