.social-networks {
  margin: auto;
  display: flex;
  justify-content: center;

  a {
    padding: 25px;

    svg {
      width: 25px;
      height: 25px;

      path {
        fill: theme(color, light);
        transition: fill 500ms;
      }
    }
  }

  /***
  *** smartphone xxl ***
  ***/
  @media #{theme(media-queries, smartphone-xxl)} {
    a:hover svg path {
      fill: theme(color, primary);
    }
  }

  /***
  *** tablet portrait ***
  ***/
  @media #{theme(media-queries, tablet-portrait)} {
    flex-direction: column;
    justify-content: space-around;
    height: 100%;

    a {
      padding: 0;
    }
  }

  /***
  *** desktop ***
  ***/
  @media #{theme(media-queries, desktop)} {
    a {
      padding: 0;

      svg {
        width: 30px;
        height: 30px;
      }
    }
  }

  /***
  *** desktop xl ***
  ***/
  @media #{theme(media-queries, desktop-xl)} {
    a {
      svg {
        width: 35px;
        height: 35px;
      }
    }
  }

  /***
  *** desktop xxl ***
  ***/
  @media #{theme(media-queries, desktop-xxl)} {
    a {
      svg {
        width: 40px;
        height: 40px;
      }
    }
  }
}

